import React, { useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";

const TreeView = function (props, ref) {
    return view(props, ref);
}

const view = (props, ref) => {
    const elements = props.elements;
    return (<div ref={ref}>
        <ul>
            {elements.map(element => (
                <li key={element.name} onClick={() => props.onSelect(element)}>
                    {element.name}
                </li>
            ))}
        </ul>
    </div>);
}

const props = [];

const WrappedComponent = wrapper(React.forwardRef(TreeView), props);
export { TreeView, WrappedComponent };